body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Navbar */
.navbar {
  padding: 1em;
  width: 100%;
}

.nav-links {
  -webkit-text-decoration: transparent;
          text-decoration: transparent;
  color: grey;
  padding: 1em;
}

.navbar a:hover {
  color: white;
}

.nav-brand {
  color: white;
}

/* Main */
.main-body {
  background: #4ca1af; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#c4e0e5),
    to(#4ca1af)
  );
  background: linear-gradient(
    to right,
    #c4e0e5,
    #4ca1af
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

/* Landing Page */
.landing-grid {
  text-align: center;
}

.avatar-img {
  height: 250px;
  padding-top: 5em;
}

.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  margin-top: 1em;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 50px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

/* About Me Page */
.aboutme-text {
  background-color: black;
  text-align: center;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  margin-top: 10em;
  border-radius: 10px;
}

.aboutme-text h1 {
  font-size: 50px;
  font-weight: bold;
  color: white;
}

.aboutme-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.aboutme-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

/* Resume Page */
.resume-body {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  margin-top: 10em;
  border-radius: 10px;
  padding: 2em;
}

.resume-body h1 {
  color: white;
  text-align: center;
}

.resume-body hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.resume-body h2 {
  color: white;
  text-align: left;
}

.resume-information {
  color: white;
  text-align: left;
}

/* Projects Page */
.projects-body {
  background-color: white;
  width: 75%;
  margin: auto;
  margin-top: 10em;
  border-radius: 10px;
  padding: 2em;
}

.projects-body h1 {
  color: grey;
}

.projects-body hr {
  border-top: 5px dotted grey;
  width: 50%;
  margin: auto;
}

.projects-grid {
  display: flex;
}

/* Contacts Page */
.contact-grid {
  text-align: center;
  justify-content: center;
  width: 45%;
  height: 500px;
  background: black;
  opacity: 0.8;
  color: white;
  margin: auto;
  margin-top: 10em;
  margin-bottom: 5em;
  border-radius: 10px;
  padding: 20px;
}

.contact-grid h2 {
  color: white;
  font-family: "Anton", sans-serif;
}

.contact-grid hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.contact-list i {
  color: white;
  font-size: 66px;
  padding-right: 5rem;
}

